.App {
    z-index: 100;

    &__border {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 5;
        width: 95vw;
        height: 90vh;
        border: 10px solid #E4FF00;
        box-sizing: border-box;
      }
}